.aboutme {
  color: #c5c5c5;
}

/* desktop */
@media screen and (min-width: 601px) {
  .image {
    border-radius: 25%;
    box-shadow: #7951d247 25px 25px 11px 0px;
    height: 400px;
  }
  a {
    text-decoration: none;
  }
}
/* mobile */
@media screen and (max-width: 601px) {
  .image {
    border-radius: 25%;
    box-shadow: #7951d247 25px 25px 11px 0px;
    height: 240px;
  }
  a {
    text-decoration: none;
  }
}
